.contact {
  &__content {
    padding: 20px;

    &__header-text {
      color: var(--theme-sub-text-color);
      display: inline-block;
      font-size: 22px;
      font-weight: 600;
      line-height: 1em;
      letter-spacing: 0.5px;
      position: relative;
      padding: 5px 10px;
      text-align: justify;
    }

    &__form {
      margin-top: 30px;
      text-align: center;

      &__controlswrapper {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 40px;
        margin-bottom: 30px;

        div {
          position: relative;

          .inputName,
          .inputEmail,
          .inputDescription {
            width: 100%;
            border: 2px solid var(--theme-main-color);
            padding: 16px 12px;
            transition: all 0.5s ease;
            position: relative;
            background-color: transparent;
            color: var(--theme-sub-text-color);
          }

          .inputDescription {
            padding-top: 24px;
          }

          .nameLabel,
          .emailLabel,
          .descriptionLabel {
            font-weight: 500;
            position: absolute;
            top: 7px;
            left: 12px;
            transition: all 0.2s ease;
            color: var(--theme-sub-text-color);
            font-size: 16px;
          }

          .inputName:focus,
          .inputEmail:focus,
          .inputDescription:focus {
            outline: none;
          }

          .inputName:focus + .nameLabel,
          .inputName:valid + .nameLabel,
          .inputEmail:focus + .emailLabel,
          .inputEmail:valid + .emailLabel,
          .inputDescription:focus + .descriptionLabel,
          .inputDescription:valid + .descriptionLabel {
            font-size: 18px;
            top: -25px;
            left: 0;
          }
        }
      }
    }

    button {
      cursor: pointer;
      padding: 10px 70px;
      font-size: 2rem;
      background: transparent;
      color: var(--theme-main-color);
      border: 1.5px solid var(--theme-main-color);
      border-radius: 4px;
      font-weight: 500;

      &:hover {
        color: #000;
        background: var(--theme-main-color);
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .contact__content__form {
    &__controlswrapper {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}
